var Swiper = require('./thirdparty/js/swiper.jquery.umd')
require('./thirdparty/css/swiper.css')
require('./App.scss')
require('./Responsive.scss')

export default function initSwiper() {
  var swiper = new Swiper('.swiper-container', {
    initialSlide: 0,
    pagination: '.swiper-pagination',
    // paginationClickable: true,
    paginationBulletRender: function (index, className) {
      if (index == 0) {
        return $("<div>").append($('<span>', {"class": className}).append($('<i>').append(''))).html()
      } else {
        return '<span class="' + className + '"></span>';
      }
    },
    direction: 'vertical',
    slidesPerView: 1,
    spaceBetween: 0,
    effect: 'fade',
    fade: {
      crossFade: false
    },
    mousewheelControl: true,
    keyboardControl: true,
    onInit: function (swiper) { //Swiper2.xonFirstInit
      $('.loading').remove();
      $('.swiper-container').css({'opacity': 1})
      $('.swiper1').addClass('swiper-slide-in')
    },
    onSlideChangeEnd: function (swiper) {
      // $('.swiper-slide').css({'opacity':1})
    },
    onSlideChangeStart: function (swiper) {
      // $('.swiper-slide').css({'opacity':1})
    }
  });
}



