var data = require('./data.json');

function resize(init = false) {
  var winW = $(window).width();
  var winH = $(window).height();
  var animatdWidth = 1200 / 12 * 7;
  if(winW>=1200 && winH<=700)
    animatdWidth = 992 / 12 * 7
  else if (winW >= 992 && winW < 1200)
    animatdWidth = 992 / 12 * 7
  else if (winW >= 768 && winW < 992)
    animatdWidth = 768 / 12 * 7
  else if (winW < 768) {
    animatdWidth = winW - 100
  }
  $('.swiper-slide .els').height(animatdWidth).width(animatdWidth)


  for (var i = 0; i < data.length; i++) {
    var page = data[i]
      , tpls = [];
    if (!page.els.length)
      continue;
    for (var j = 0; j < page.els.length; j++) {
      var el = page.els[j],
        css = {
          left: el.left / 700 * animatdWidth,
          top: el.top / 700 * animatdWidth,
          width: el.width / 700 * animatdWidth,
          height: el.height / 700 * animatdWidth
        }
        , img_css = {
          width: el.width / 700 * animatdWidth,
          height: el.height / 700 * animatdWidth
        };

      if (init) {
        var $text = el.class == 'el-text-countdown' ?
          $('<span>', {
            "class": 'text',
            "text": el.text
          }) : '';
        var $img = el.src ?
          $('<img>', {src: el.src, css: img_css}) : '';

        var $el = $('<div>', {
          "class": 'el' + (el.class ? ' ' + el.class : ''),
          "css": css
        });
        var $ani = $('<div>', {
          "class": 'ani'
        });
        var $ani2 = $('<div>', {
          "class": 'ani2'
        });

        if(typeof el.els != "undefined"){
          var $els_el = [];
          for (var k=0;k<el.els.length;k++){
            var els_el = el.els[k];
            $els_el[k] = $('<div>', {
              "class": 'el' + (els_el.class ? ' ' + els_el.class : ''),
              "css": {
                left: els_el.left / 700 * animatdWidth,
                top: els_el.top / 700 * animatdWidth,
                width: els_el.width / 700 * animatdWidth,
                height: els_el.height / 700 * animatdWidth
              }
            });
          }
          $ani2.append($els_el);
        }

        tpls[j] = $el.append($ani.append($ani2.append($img).append($text)))
      } else {
        $('.swiper-slide:eq(' + i + ') .els .el:eq(' + j + ')').css(css)
        $('.swiper-slide:eq(' + i + ') .els .el:eq(' + j + ') img').css(css)
      }
    }
    if (init)
      $('.swiper-slide:eq(' + i + ') .els').prepend(tpls)
  }
}

export default resize;


