var time = 3000;
var index = 0;
var timer = function(){
  setTimeout(function(){
    index++;
    if (index >= $('.box-list li').length)
      index = 0;
    $('.box-list li').removeClass('active');
    $('.box-list li').eq(index).addClass('active');
    timer();
  }, time);
}
export default function carousel() {
  var winW = $(window).width();
  if (winW >= 768)
    return;
  $('.box-list li').eq(index).addClass('active');
  timer()
}


